.card {
  display:grid;
  margin:50px;
  width:300px;
  grid-template-columns: 300px;
  /* grid-template-rows:  210px auto 42px; */
  grid-template-areas: "image" "text" "stats";
  border-radius:18px;
  background: white;
  border: grey 1px solid;
  box-shadow: 5px 5px 15px rgba(1,1,1,1);
  text-align: center;
  position: relative;
}

.card-image {
  grid-area: image;
}
.card-text {
  grid-area: text;
}
.card-stats {
  grid-area: stats; 
}

.round-button{
  border-radius: 12px;
  border:solid 1px black;
}

.card-image {
  grid-area: image;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  background-size: cover;
}
.card-text {
  grid-area: text;
  margin: 25px;
}
.card-text .date {
  color: rgb(255, 7, 110);
  font-size:13px;
}
.card-text p {
  color: grey;
  font-size:15px;
  font-weight: 300;
}
.card-text h2 {
  margin-top:0px;
  font-size:28px;
}

.card-stats {
  grid-area: stats; 
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  background: rgb(220, 182, 168);
}

.card-stats .stat {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: white;
  padding:10px;
}

.card-stats .type {
font-size: 11px;
font-weight: 300;
text-transform: uppercase ;
}

.card-stats .value {
  font-size: 12px;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  }

.card-stats .border {
  border-left: 1px solid black;
  border-right: 1px solid black;
}
  

.card:hover {
  transform: scale(1.15);
  box-shadow: 5px 5px 15px rgba(0,0,0,1);
}

